import Helper from '@/function.js';
export default {
	data() {
		return {
			token:"",
			list:[]

		}


	},
	onLoad:function(){
		this.token = uni.getStorageSync('token');
		
	},
	onShow:function(){
		this.list = []
		this.getList();
	},
	methods: {
		delBank(item, index){
			var _this = this;
			uni.showModal({
				content:"是否确认删除银行卡",
				success:function(res){
					if (res.confirm){
						uni.showLoading({
							title: "请稍候..."
						})
						uni.request({
							url: _this.ApiGateWay + '/sale/Member/MemberDelBankAccount?id='+item.id,
							data:"",
							method: 'POST',
							dataType: 'json',
							header: {
								Authorization: _this.token
							},
							success: function(res) {
								console.log(res);
									
								if (Helper.CheckData(res)) {
									uni.showModal({
										content:"删除成功",
										showCancel:false
									});
									_this.list.splice(index, 1)
								} else {
									
								}
							},
							fail: function() {
								uni.hideLoading();
							},
							complete: function() {
								uni.hideLoading();
							}
									
									
						})
					}
				}
			})
			
			
		},
		getList() {
			var _this = this;
			uni.showLoading({
				title: "请稍候..."
			})
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/MemberGetBankAccountList',
				data: {
					pageIndex:1,
					pageSize:100,
				},
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res);
		
					if (Helper.CheckData(res)) {
						_this.list = res.data.list
					} else {
		
					}
				},
				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
				}
		
		
			})
		},
		goAddBank(item) {
			uni.navigateTo({
				url: '/pages/my/addbank?item=' + JSON.stringify(item)
			})
		},
		goBank() {
			uni.navigateBack({
				delta: 1
			})


		}


	}


}
